$calendar-padding: 10px;
$calendar-cell-size: 100px;
$calendar-cell-gap: 1px;
$calendar-cell-bg-color: #eee;
$calendar-cell-border-color: #000;
$calendar-cell-font-size: 30px;
$calendar-cell-hover-bg-color: #ffc0cb;
$calendar-cell-hover-shift: -20px;
$calendar-cells-name:
  1 "mon",
  2 "tue",
  3 "wed",
  4 "thu",
  5 "fri",
  6 "sat",
  7 "sun";
